import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const { TextArea } = Input;
import UploadImg from "./uploadImg.jsx"
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.type == "detail" ? true : false,
            title: "",
            img: {
                file: {},
                fileList: []
            },
            time: "",
            status: "1",
            link: "",
            loading: true
        };
        ['checkImg', 'handleSubmit', 'resetForm', 'showLoading', 'hideLoading'].forEach(m=>this[m] = this[m].bind(this))
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }
    componentDidMount() {
        let that = this;
        if(this.props.type == "add") {
            this.setState({loading: false});
            that.KE = window.K.create('#content', {
                allowFileManager : true,
                afterChange: function() {
                    that.props.form.setFieldsValue({"content" :this.html()});
                }
            });
        } else {
            ajax({
                url: "/notice/getInfoDetail",
                method: "post",
                data: {
                    id: this.props.noticeID
                },
                callback: function(data) {
                    if(data.status == 200) {
                        that.setState({
                            loading: false,
                            title: data.data.detailInfo.title,
                            img: {
                                file: {},
                                fileList: [{
                                    uid: -1,
                                    name: data.data.detailInfo.img.split("/").pop(),
                                    status: "done",
                                    url: data.data.detailInfo.img
                                }]
                            },
                            time: data.data.detailInfo.time,
                            status: data.data.detailInfo.status,
                            link: data.data.detailInfo.link
                        });
                        that.KE = K.create('#content', {
                            allowFileManager : true,
                            afterChange: function() {
                                that.props.form.setFieldsValue({"content" :this.html()});
                            }
                        });
                        that.KE.html(data.data.detailInfo.content);
                        if(that.state.disabled) {
                            that.KE.readonly();
                        }
                        if(that.state.disabled) {
                            $(".anticon-delete").remove();
                        }
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            });
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type == "edit") {
                    values = Object.assign(values, {id: this.props.noticeID});
                }
                values.img = values.img.fileList[0].response ? values.img.fileList[0].response.data.imgUrl : values.img.fileList[0].url;
                ajax({
                    url: "/notice/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1, function() {
                                hashHistory.push("/notice")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    returnList() {
        hashHistory.push("/notice");
    }

    resetForm() {
        this.setState({
            fileList: []
        });
        this.props.form.resetFields();
    }

    checkImg(rule, value, callback) {
        if(value.fileList.length > 0 && value.fileList[0].status == "done") {
            callback();
            return;
        }
        callback("请选择图片!");
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 6 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="标题"
                        hasFeedback
                    >
                        {getFieldDecorator('title', {
                            rules: [
                                { required: true, message: '请输入标题!' }
                            ],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="图片"
                        hasFeedback
                    >
                        {getFieldDecorator('img', {
                            rules: [{
                                validator: this.checkImg
                            }],
                            initialValue: this.state.img
                        })(
                            <UploadImg showLoading={this.showLoading} hideLoading={this.hideLoading} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="状态"
                    >
                        {getFieldDecorator('status', {
                            initialValue: this.state.status.toString()
                        })(
                            <RadioGroup disabled={this.state.disabled}>
                                <Radio value="0">无效</Radio>
                                <Radio value="1">有效</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="活动详情"
                        hasFeedback
                    >
                        {getFieldDecorator('content', {
                            rules: [
                                {
                                    required: true,
                                    message: "请输入内容!"
                                }
                            ],
                            initialValue: this.state.content
                        })(
                            <TextArea disabled={this.state.disabled} placeholder="这里输入内容" autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="链接地址"
                    >
                        {getFieldDecorator('link', {
                            rules: [
                                { required: true, message: '请输入链接!' }
                            ],
                            initialValue: this.state.link
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        { !this.state.disabled ? <Button type="primary" htmlType="submit" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""}

                        <Button onClick={this.returnList} style={{"marginRight": "20px"}}>取消</Button>
                        { !this.state.disabled ? <Button type="danger" onClick={this.resetForm}>重置</Button> : ""}
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


